// Fonts
@import url('https://fonts.googleapis.com/css2?family=Lato:ital,wght@0,100;0,300;0,400;0,700;0,900;1,100;1,300;1,400;1,700;1,900&family=Reenie+Beanie&display=swap');

// Color Variables
$primary: #666;
$secondary: #fff;
$alt: #000;

// Fonts
$display: "Reenie Beanie", cursive;
$text: "Lato", sans-serif;

/* Base Styling */
*, *::before, *::after {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body {
    margin: 20px auto;
    font-family: $text;
    background-color: $primary;
    color: $secondary;
}

h1, h2, h3, h4, h5, h6 {
    font-family: $text;
    font-weight: bold;
}

h2 {
    font-size: 2rem;
}

p {
    font-family: $display;
    font-size: 2rem;
}

ul, li {
    list-style: none;
}

ul {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
}

ul li a {
    text-decoration: none;
    color: $alt;
    background: #ffc;
    display: block;
    height: 10em;
    width: 10em;
    padding: 1em;
}

ul li {
    margin: 1rem;
}